
.contact {
  padding: 80px 100px 100px;
  display: flex;
  justify-content: center;
  margin-left: 365px;
  margin-right: 365px;

  .contact-column1 {
    height: 376px;
    width: 50%;

    .contact-title {
      text-align: left;
      font-size: 3.0625rem;
      line-height: 3.6875rem;
      font-family: $font-raleway;
      color: $color-black;
      margin-bottom: 22px;

    }

    .contact-description {
      text-align: left;
      font-size: 1rem;
      color: $color-black;
      line-height: 1.375rem;
      margin: 22px 0 42px;
    }

    .contact-details {
      display: inline-block;
      margin-top: 42px;

      .contact-point {
        margin-right: 20px;

        img {
          margin-right: 10px;
        }
      }


    }

  }

  .contact-column2 {
    height: 376px;
    width: 50%;

    .form {
      display: flex;
      flex-direction: column;

      &:first-of-type {
        margin-bottom: 42px;
      }

      .form__place {
        display: flex;
        flex-direction: column;

        .form__label {
          font-weight: bold;
          text-transform: uppercase;
          color: $color-primary;
          line-height: 0.9rem;
          letter-spacing: 3px;
          font-size: 0.6875rem;
        }

        .form__input {
          border: none;
          border-bottom: 1px solid $color-grey;
          background-color: transparent;
          line-height: 2.625rem;
        }
      }

      .form__consent {
        margin-top: 30px;

        .form__checkbox {
          display: block;
          position: relative;
          padding-left: 35px;
          margin-bottom: 12px;
          cursor: pointer;
          font-size: 1.375rem;

          input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
          }

          .checkbox {
            position: absolute;
            top: 0;
            left: 0;
            height: 25px;
            width: 25px;
            background-color: $color-white;
            border: 1px solid $color-grey;
            border-radius: 4px;
          }

          .consent__text {
            margin: 0 10px;
            font-size: 0.75rem;
            color: $color-grey;
            line-height: 17px;
          }
        }
      }

      .btn {
        font-size: 0.8125rem;
        padding: 20px 97px;
        align-self: flex-end;
        margin-top: 20px;
        border: none;
        background-color: $color-primary;
        color: $color-white;

      }
    }
  }
}


